//搜索框
import { Button} from 'antd-mobile';
import React from'react'
import {withRouter} from 'react-router-dom'
class SeaDemo extends React.Component{
    constructor(props) {
      super(props);
  }
    render(){
      return(
        <div>
           <Button onClick={()=>{this.props.history.push('/home/SearchBar')}} style={{backgroundColor:'orangered',color:'white'}}>
           <i className="iconfont icon-fangdajing" ></i>
           寻找宝贝店铺
           </Button>
       </div>
      );
    }
  }
  export default withRouter (SeaDemo);