import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if(pathname.split('/').length>2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 20
            } }>
                <TabBar
                    unselecte        dTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home" 
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-icon-'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon-'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao'></i>                           
                        }
                        title="订单列表"
                        key="list"
                        selected={ this.state.selectedTab === '/list' }
                        onPress={ () => {
                            this.setState({
                                selectedTab:'list',
                            });
                            this.props.history.push('/list')
                        } }
                        data-seed="logId1"
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-geren'></i> }
                        selectedIcon={
                            <i className='iconfont icon-geren'></i>                           
                        }title="我的淘宝"
                        key="me"
                        selected={ this.state.selectedTab === '/me' }
                        onPress={ () => {
                            this.setState({
                                selectedTab:'me',
                            });
                            this.props.history.push('/me')
                        } }
                        data-seed="logId1"
                    >
                        
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);