import React from 'react';
import { Button, TabBar } from 'antd-mobile';
export default class PicDemo extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <div>
                    <i className='iconfont icon-fanhui' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                    <img src={require('../image/caidao2.png')}
                    alt=""
                    style={{ width:300,height:300,verticalAlign: 'top' }}/> 
                </div>
                <div style={{padding:10,height:60,marginTop:200}}>
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-dianpu' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                        <p>店铺</p>
                    </button>        
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-kefu'  onClick={()=>this.props.history.push('/home')}></i>
                        <p>客服</p>
                    </button>   
                    <button onClick={()=>{this.props.history.push('/')}}>
                        <i className='iconfont icon-shoucang' style={{display:"block"}} onClick={()=>this.props.history.push('/home')}></i>
                        <p>收藏</p>
                    </button>  
                    <button onClick={()=>{this.props.history.push('/')}} style={{width:100,backgroundColor:'orange'}}>  
                    <p>加入购物车</p>
                    </button>  

                    <button onClick={()=>{this.props.history.push('/')}} style={{width:100,backgroundColor:'red'}}>
                        <p>立即购买</p>
                    </button>   
                </div>
                
            </div>
        )
    }
}