import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';
import {withRouter} from 'react-router-dom';
import SeaDemo from './SeaDemo';
import Pic from './Pic';

const Home = () => {
    return (
      <div>
        <div style={{height:4},{backgroundColor:'orange'}}>
            <i className='iconfont icon-taobao2' style={{float:'left'}}></i>
            <SeaDemo/>
          </div>
          <div>
            <CarDemo/>
          </div>
          <div>
          <img src={require('../image/like.png')}
                    alt=""
                    style={{ width:'100%',height:100 }}/>
          </div>
          <div>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
            <Pic/>
          </div>
      </div>
  )
}

//轮播图
class CarDemo extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['45/O1CN01rNyxVF1CCeS74h08l_!!45-0-ppp-picassogw','172/O1CN018tFofI1D8oksbLs1A_!!172-0-lubanu','86/O1CN01Cf8h5C1CVQh3rmEtC_!!86-0-lubanu' ]
      });
    }, 1000);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://gw.alicdn.com/imgextra/i4/${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    ); 
  }
}

// ReactDOM.render(<App />, mountNode);
export default Home