import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Me from './taobao/Me';
import List from './taobao/List';
import Pic from './taobao/Pic';
import PicDemo from './taobao/PicDemo';
import SearchBarExample from './taobao/SearchBar';
import 'antd-mobile/dist/antd-mobile.css';

const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/me' component={Me} />
			<Route path='/list' component={List} />
			<Route path='/pic' component={Pic} />
			<Route path='/picdemo' component={PicDemo} />
			<Route path='/home/detail' component={Cart} />
			<Route path='/home/SearchBar' component={SearchBarExample}/>
			<Route render={()=><Redirect to='/'/>} />
		</Switch>
		<Mytab />
	</Router>
}
export default App;

function PrivateRoute({ routes,component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com routes={routes}/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}

export const RouteWithSubRoute = (props)=>{
	if(props.pri){
		return <PrivateRoute {...props}/>
	}
	return <Route 
				path={props.path} 
				render={
					(pro)=><props.component {...pro} routes={props.routes}/>
				}
			/>
}
 
